<template>
  <div class="index_page" @click="action = ''">
    <div class="top">
      <div style="float: left;">
        <img style="width: 40px;" src="./assets/image/logo.png" alt="">
      </div>
      <div class="top_right" @click.stop>
        <div style=" text-align: right;">
          <template v-if="user">
            <img :src="baseUrl + user.imgUrl"
                 style="width: 30px; height: 30px; border-radius: 15px; position: relative; top: 7px;">
            {{user.nickName}}
          </template>
          <template v-if="!user">
            <Button type="ghost" @click.stop="action = 'login'">登录</Button>
            <Button type="ghost" @click.stop="action = 'register'">注册</Button>
          </template>
        </div>
        <Card v-show="action == 'login'" style="width: 300px; margin-top: 10px;"
              dis-hover>
          <Form ref="formLogin" :model="formLogin" :rules="ruleLogin">
            <Form-item prop="userName">
              <Input type="text" v-model="formLogin.userName" placeholder="用户名">
              </Input>
            </Form-item>
            <Form-item prop="password">
              <Input type="password" v-model="formLogin.password" placeholder="密码">
              </Input>
            </Form-item>
            <Button type="primary" :loading="loginLoading" @click="handleSubmit('formLogin', 'login', formLogin)">登录
            </Button>
          </Form>
        </Card>
        <Card v-show="action == 'register'" style="width: 300px; margin-top: 10px;"
              dis-hover>
          <Form ref="formRegister" :model="formRegister" :rules="ruleRegister">
            <Form-item prop="userName">
              <Input type="text" v-model="formRegister.userName" placeholder="用户名">
              </Input>
            </Form-item>
            <Form-item prop="password">
              <Input type="password" v-model="formRegister.password" placeholder="密码">
              </Input>
            </Form-item>
            <Form-item prop="email">
              <Input type="text" v-model="formRegister.email" placeholder="邮箱">
              </Input>
            </Form-item>
            <Button type="primary" :loading="registerLoading"
                    @click="handleSubmit('formRegister', 'register', formRegister)">注册
            </Button>
          </Form>
        </Card>
      </div>
    </div>
    <div class="card animated fadeInUp">
      <div>
        <img src="./assets/image/logo-font.png" alt="">
      </div>
      极速 Mock 工具
      <div>
        <Button type="ghost" shape="circle" style="margin-right: 50px;">使用</Button>
        <Button type="ghost" shape="circle">文档</Button>
      </div>
    </div>
  </div>
</template>
<script>
  import getData from './config/getData'
  import { setNew, getUser } from './config/data'
  import baseUrl from './config/baseUrl'
  export default {
    created () {
      getUser().then(user => {
        this.user = user
      })
    },
    data () {
      return {
        baseUrl,
        user: null,
        loginLoading: false,
        registerLoading: false,
        action: '',
        formLogin: {
          userName: '',
          password: ''
        },
        ruleLogin: {
          userName: [
            { required: true, message: '请填写用户名', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'blur' }
          ]
        },
        formRegister: {
          userName: '',
          password: '',
          email: ''
        },
        ruleRegister: {
          userName: [
            { required: true, message: '请填写用户名', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'blur' },
            { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
          ],
          email: [
            { required: false, message: '邮箱不能为空', trigger: 'blur' },
            { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleSubmit (name, type, data) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if (type === 'register') {
              this.registerLoading = true
            } else {
              this.loginLoading = true
            }
            getData(type, { ...data }).then(() => {
              if (type === 'register') {
                this.formLogin.userName = this.formRegister.userName
                this.formLogin.password = this.formRegister.password
                this.handleSubmit('formLogin', 'login', this.formLogin)
              } else {
                setNew()
                this.$router.push({
                  path: '/app/project'
                })
              }
            }, res => {
              if (type === 'register') {
                this.registerLoading = false
                this.$Message.error('注册失败')
              } else {
                if (res.data.code === 601) {
                  this.$Message.error('密码错误')
                } else {
                  this.$Message.error('登陆失败')
                }
                this.loginLoading = false
              }
            })
          }
        })
      }
    }
  }
</script>



